<template>
  <div>
    <div style="padding-top: 40px; background: #f1f1f3">
      <div class="row justify-content-center" style="margin-bottom: 30px">
        <div class="col-md-8">
          <h3 class="text-center mb50" style="font-family: fantasy">Money management Page</h3>
          <div class="card">
            <div class="card-body">
              <form @submit.prevent="handleSubmitForm" class="form-style">
                <div class="form-group mr20">
                  <label class="form-label">Insert a name</label>
                  <input type="text" class="form-control" v-model="money.name" placeholder="Management Name" required />
                </div>
                <div class="form-group mr20">
                  <label class="form-label">Starting Bank</label>
                  <input type="text" class="form-control" v-model="money.startingBank" placeholder="€10000" required />
                </div>
                <div class="form-group mr20">
                  <label class="form-label">Target profit</label>
                  <input type="text" class="form-control" v-model="money.targetProfit" placeholder="10%" required />
                </div>
                <div class="form-group mr20">
                  <label class="form-label">Stop Losses</label>
                  <input type="text" class="form-control" v-model="money.stopLoss" placeholder="3%" required />
                </div>
                <div class="form-group mr20 mt-auto">
                  <button class="btn btn-success">Add</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
      <div class="money-table">
        <div class="card">
          <div class="card-body">
            <div class="label_1">
              <label>Money manangement list:</label>
            </div>
            <div class="col-md-12">
              <table class="table table-striped" style="text-align: center; font-family:'Poppins', sans-serif">
                <thead class="thead-dark">
                  <tr>
                    <th>Name</th>
                    <th>Starting Bank(€)</th>
                    <th>Target Profit(%)</th>
                    <th>Stop Loss(%)</th>
                    <th>P / L %</th>
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="money in Moneys" :key="money._id">
                    <td>{{ money.name }}</td>
                    <td>{{ money.startingBank }}</td>
                    <td>{{ money.targetProfit }}</td>
                    <td>{{ money.stopLoss }}</td>
                    <td>{{ money.PL }}</td>
                    <td>
                      <router-link :to="{ name: 'detail', params: { id: money._id } }" class="btn btn-success">Add </router-link>
                      <button @click.prevent="deleteList(money._id)" class="btn btn-danger">Delete</button>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import swal from "sweetalert2";
export default {
  data() {
    return {
      money: {
        name: "",
        startingBank: "",
        targetProfit: "",
        stopLoss: "",
      },
      Moneys: [],
      moneyId: "",
    };
  },
  created() {
    let apiURL = "http://localhost:8081/api/moneys/list";
    axios
      .post(apiURL)
      .then((res) => {
        this.Moneys = res.data;
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods: {
    handleSubmitForm() {
      let apiURL = `http://localhost:8081/api/moneys/searchName/${this.money.name}`;
      axios
        .post(apiURL)
        .then((res) => {
          if (res.data != "") {
            swal.fire("Alert!", "This name is already existed. Please input another name", "error");
          } else {
            let apiURL = "http://localhost:8081/api/moneys/create";
            axios
              .post(apiURL, this.money)
              .then((res) => {
                swal.fire("Success!", "Create new money management data succesfully", "success");
                this.money = {
                  name: "",
                  startingBank: "",
                  targetProfit: "",
                  stopLoss: "",
                };
                let apiURL = "http://localhost:8081/api/moneys/list";
                axios
                  .post(apiURL)
                  .then((res) => {
                    this.Moneys = res.data;
                  })
                  .catch((error) => {
                    console.log(error);
                  });
                this.moneyId = res.data._id;
                const bank = res.data.startingBank;
                const id = this.moneyId;
                const data = { id, bank };
                let detailURL = "http://localhost:8081/api/details/create";
                axios
                  .post(detailURL, data)
                  .then(() => {})
                  .catch((error) => {
                    console.log(error);
                  });
              })
              .catch((error) => {
                console.log(error);
              });
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    addDetail(id) {
      let indexOfArrayItem = this.Moneys.findIndex((i) => i._id === id);
      console.log(indexOfArrayItem);
      this.$router.push("/detail");
    },
    deleteList(id) {
      let apiURL = `http://localhost:8081/api/moneys/${id}`;
      let indexOfArrayItem = this.Moneys.findIndex((i) => i._id === id);
      swal
        .fire({
          title: "Are you sure?",
          text: "You won't be able to revert this!",
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Yes, delete it!",
        })
        .then((result) => {
          if (result.value) {
            axios
              .delete(apiURL)
              .then(() => {
                this.Moneys.splice(indexOfArrayItem, 1);
                swal("Deleted!", "Your file has been deleted.", "success");
              })
              .catch((error) => {
                console.log(error);
              });
          }
        });
    },
  },
};
</script>

